import PropTypes from 'prop-types';

const ctx = require.context('../static/img', true, /\.(png|jpe?g|svg)$/);

const Image = ({ src, alt }) => {
  const props = {
    src: ctx(src.replace('../static/img/', './')),
    alt: alt
  };
  return <img {...props} />;
};

Image.propTypes = {
  src: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired
};

export default Image;
